import React from 'react'

import logo from '../../../src/siddhivinayak.png';
import background from "../../../src/bg.jpg"

function Start() {
    return (
        <div className="start section" style={{ backgroundImage: `url(${background})`, backgroundRepeat: 'no-repeat'}}>
            <h2>
            Welcom to Soham Logistics React Demo!
            </h2>
            <img src={logo} className="App-logo center" alt="logo" />
            
            <button className="button startButton center" 
            
            >Start
            </button>
        </div>
    )
}

export default Start
